import React, { Component } from "react";

export default class Timeline extends Component {
  render() {
    return (
      <div>
        <section className="colorlib-experience" data-section="timeline">
          <div className="colorlib-narrow-content">
            <div className="row">
              <div
                className="col-md-6 col-md-offset-3 col-md-pull-3 animate-box"
                data-animate-effect="fadeInLeft"
              >
                <span className="heading-meta">highlights</span>
                <h2 className="colorlib-heading animate-box">Timeline</h2>
              </div>
            </div>
            <div className="row">
              <div className="col-md-12">
                <div className="timeline-centered">
                  <article
                    className="timeline-entry animate-box"
                    data-animate-effect="fadeInTop"
                  >
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-4">
                        <i className="icon-pen2" />
                      </div>
                      <div className="timeline-label">
                        <h2>
                          Mobile and Web Full Stack Developer{" "}
                          <span>2019-Present</span>
                        </h2>
                        <p>
                          As a mobile and web Full Stack Developer, I've created
                          multiple applications. I had self-learned some of the
                          useful tools and technologies to reach out the best
                          results on developing both the client-side and
                          server-side of these applications. I'm often using
                          tools like JavaScript, React, React Native, Node.js,
                          Firebase, Mongo, etc. Feel free to visit my Github
                          page to watch some of these projects
                        </p>
                      </div>
                    </div>
                  </article>
                  <article
                    className="timeline-entry animate-box"
                    data-animate-effect="fadeInLeft"
                  >
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-3">
                        <i className="icon-pen2" />
                      </div>
                      <div className="timeline-label">
                        <h2>
                          Application Engineer at Quality Forward{" "}
                          <span>2019-2021</span>
                        </h2>
                        <p>
                          Quality Forward is an early stages startup in the
                          E-QMS field. My responsibilities were developing new
                          features, taking care of customer requirements,
                          performing integrations etc. Some of my work involve
                          coding and some involve communication with the
                          customers in order to get wider understanding of their
                          needs and to supply the best result in the system. In
                          my work I used JS, Ajax, Jquery, HTML, Python, Web
                          Services and more.
                        </p>
                      </div>
                    </div>
                  </article>
                  <article
                    className="timeline-entry animate-box"
                    data-animate-effect="fadeInLeft"
                  >
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-5">
                        <i className="icon-pen2" />
                      </div>
                      <div className="timeline-label">
                        <h2>
                          Undergraduate at Ben-Gurion University{" "}
                          <span>2016-2020</span>
                        </h2>
                        <p>
                          I did my bachelor's degree in Industrial Engineering
                          majoring in Information Systems. I have taken courses
                          like OOP, DBs, Algorithms, Machine Learning, etc. I
                          have also taken some external courses on mobile
                          applications development and web development
                        </p>
                      </div>
                    </div>
                  </article>
                  <article
                    className="timeline-entry begin animate-box"
                    data-animate-effect="fadeInBottom"
                  >
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-none"></div>
                    </div>
                  </article>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}
